<template>
    <div class="body">
        <div class="canvasBackground">
            <canvas id="BackgroundCanvas" width="100vw" height="100vh"></canvas>
        </div>

        <div class="btns">
            <img src="../assets/utils/Arrow_L.svg" class="imgArrow" id="Arrow_L" alt="">
            <input type="radio" name="view" onclick="window.location.href='#sec1'" id="btn1">
            <input type="radio" name="view" onclick="window.location.href='#sec2'" id="btn2">
            <!-- <input type="radio" name="view" onclick="window.location.href='#sec3'" id="btn3"> -->
            <img src="../assets/utils/Arrow_R.svg" class="imgArrow" id="Arrow_R" alt="">
        </div>
        <div class="sections">
            <section id="sec1">
                <div class="parent">
                    <div class="child">
                        <h1>WELCOME</h1>
                        <p>I'm David Diaz, if you want to know more about me, I invite you to explore my website</p>
                    </div>
                </div>
            </section>
            <section id="sec2">
                <div class="parent">
                    <div class="child">
                        <h1>ABOUT ME</h1>
                        <p>I am a novice developer, my experience is due to personal practice projects, in addition to some courses that I have taken. <br> I have experience in fields such as: back-end, front-end, database, using javascript as a programming
                            language,
                            <br> I have also developed Java applications with the Eclipse environment as course projects at the university. <br> I have experience with technologies such as: Git, Boostrap, Node.Js, Vue.Js, PostgreSQL, MySQL</p>
                        <div class="socialNetworks">
                            <a href="https://www.facebook.com/daivdiz" target="_blank"><img src="../assets/social/facebook.png" alt=""></a>
                            <a href="https://twitter.com/daivdiz" target="_blank"><img src="../assets/social/twitter.png" alt=""></a>
                            <a href="https://github.com/daivdiz" target="_blank"><img src="../assets/social/github.png" alt=""></a>
                            <a href="https://www.linkedin.com/in/daivdiz" target="_blank"><img src="../assets/social/linkedin.png" alt=""></a>
                            <a href="https://www.instagram.com/daivdiz/" target="_blank"><img src="../assets/social/instagram.png" alt=""></a>
                        </div>
                    </div>
                </div>
            </section>
        </div>
    </div>
</template>

<script>
// import * as index from './index';
export default{
    mounted(){
        var btns = document.getElementsByTagName('input');
        var views = document.getElementsByTagName('section');
        var cont = 1;
        var aL = document.getElementById('Arrow_L');
        var aR = document.getElementById('Arrow_R');
        location.href = "#sec1";
        btns[0].checked = "true";

        window.addEventListener('keydown', (e) => {
            if (e.keyCode === 39) {
                comp("+");
            }
            if (e.keyCode === 37) {
                comp("-");
            }
        })

        aL.addEventListener('click', () => {
            comp("-")
        });
        aR.addEventListener('click', () => {
            comp("+");
        })

        var canvas = document.getElementById('BackgroundCanvas');
        var ctx = canvas.getContext('2d');

        canvas.width = window.innerWidth;
        canvas.height = window.innerHeight;

        var w = canvas.width;
        var h = canvas.height;

        var draw = function(){
            ctx.lineWidth = 1;
            ctx.fillStyle = "black";
            ctx.fillRect(0,0,w,h);
            
            for(let i = -200; i < 180; i+=(h/100)) {
                ctx.strokeStyle = 'rgba(255,125,0,0.8)';
                ctx.beginPath();
                ctx.moveTo(0,(h/2));
                for(let j = -w; j<w;j+=(w/200)){
                    ctx.lineTo(5 * Math.cos(i) +
                    j + (0.01 * 100 * j),
                    Math.floor(h / 2 + j / 2 *
                        Math.cos(j / 50 - t / 50 - i / 118) +
                        (i * 0.9) * Math.cos(j / 25 - (i + t) / 65)))
                }
                ctx.stroke();
            }

        }
        var t = 0;
        var run = function(){
            window.requestAnimationFrame(run);
            t+=5;
            draw()
        }
        run();
        function comp(op) {
            if (op == "+") {
                cont++;
            }
            if (op == "-") {
                cont--;
            }
            if (cont > views.length) {
                cont = 1;
            }
            if (cont < 1) {
                cont = views.length;
            }

            btns[cont - 1].checked = "true";
            location.href = "#sec" + cont.toString();
        }
    }
}
</script>

